import React, { useEffect, useState } from 'react';
import 'antd/dist/antd.css';
import './CardBank.css';
import { getRefreshTokin, getBankApi, keys, sendFilter } from '../api/api';
import { Button, Modal, Select, message, Radio, Input, Form } from 'antd';
import SortAscendingOutlined from '@ant-design/icons/lib/icons/SortAscendingOutlined';
import SortDescendingOutlined from '@ant-design/icons/lib/icons/SortDescendingOutlined';

const CardBank = () => {
  //Для всплывающего модального окна
  const filterObj: any = {};
  const [visible, setVisible] = useState(false);
  //Для вывода в модальное окно всей инфы по банкам
  const [data, setData] = useState<any>([]);
  //Для изменения названия тега
  const { Option } = Select;
  //Для всплывающего сообщения
  const [bankData, setBankdata] = useState<any>([]);
  //Для всплывающего сообщения
  const key = 'updatable';

  //Срабатывает при вводе цифр
  //Для создания массива фильтра и отправки на сервер для фильтрации по банкам
  const onInputChange = (e: any) => {
    //Если значение пусто то пишем в нем "0"
    if (e.target.value) {
      filterObj[e.target.name] = [e.target.name, '>=', e.target.value];
    } else {
      filterObj[e.target.name] = [e.target.name, '>=', 0];
    }
    //отправка на бд
    sendFilter(filterObj).then((res) => {
      setBankdata(res);
      message.success({ content: 'Готово!', key, duration: 1 });
    });
  };
  ///

  //Для overstatement создание массива и отправка
  const optionsClick = (e: any) => {
    filterObj['overstatement'] = e;

    sendFilter(filterObj).then((res) => {
      setBankdata(res);
      message.success({ content: 'Готово!', key, duration: 1 });
    });
  };
  ////

  //для да, нет и для сортировки по алфавиту
  const onFilterClick = (e: any) => {
    filterObj[e.target.name] = e.target.value;

    sendFilter(filterObj).then((res) => {
      setBankdata(res);
      message.success({ content: 'Готово!', key, duration: 1 });
    });
  };
  ////

  const catchError = () => {
    getRefreshTokin()
      .then((res: any) => res.json())
      .then((res) => {
        localStorage.access_token = res.access_token;
        bankApi();
      })
      .catch(() => {
        localStorage.removeItem('access_token');
      });
  };
  const bankApi = () => {
    getBankApi()
      .then((res: any) => res.json(), message.loading({ content: 'Загрузка...', key }))
      .then((res: any) => {
        if (res.message) {
          catchError();
        } else {
          setBankdata(res);
          message.success({ content: 'Готово!', key, duration: 2 });
        }
      })
      .catch(catchError);
  };

  useEffect(() => {
    bankApi();
  }, []);

  return (
    <>
      <section className="wrapper__bank-section">
        <div className="card__wrapper">
          <div className="bank__wrapper">
            {bankData?.map((i: any) => {
              return (
                <div key={i?.id} className="bank">
                  <div className="bank__img">
                    <img src="https://loremflickr.com/640/360" alt="" />
                  </div>
                  <div className="bank__cotent">
                    <div className="bank__title">{i?.title?.type}</div>
                    <div className="bank__text"></div>
                    <Button
                      onClick={() => {
                        setVisible(true);
                        setData(i);
                      }}
                    >
                      Подробнее
                    </Button>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
        <>
          {/* <Button
            onClick={() => {
              console.log('f', filterObj);
              filterObj = {};
              console.log('f2', filterObj);
            }}
          >
            Очистить фильтр
          </Button> */}
          <Modal
            title={data?.title?.type}
            centered
            visible={visible}
            onOk={() => setVisible(false)}
            onCancel={() => setVisible(false)}
            width={1200}
            className="modal__bank"
          >
            {Object.entries(data).map((i: any) => {
              if (
                i[1].title === 'Название банка' ||
                i[1].title === 'Логотип' ||
                i[1].title === 'Дата изменения ' ||
                i[1].title === undefined
              ) {
                return null;
              } else {
                if (i[1]?.filter !== undefined && i[1]?.num === undefined) {
                  return (
                    <p>
                      <strong>
                        {i[1].title} :{' '}
                        <strong style={{ color: 'red' }}>{i[1]?.filter ? ' Да' : ' Нет'}</strong>
                      </strong>
                      <div style={{ marginBottom: 10 }}>{i[1]?.comment ? 'Коментарии' : null}</div>
                      <div>{i[1]?.comment ? i[1]?.comment : null}</div>
                    </p>
                  );
                } else if (i[1]?.type !== undefined) {
                  return (
                    <p>
                      <strong>
                        {i[1].title} :
                        <strong style={{ color: 'red' }}>{i[1]?.type ? i[1]?.type : null}</strong>
                      </strong>
                      <div style={{ marginBottom: 10 }}>{i[1]?.comment ? 'Коментарии' : null}</div>
                      <div>{i[1]?.comment ? i[1]?.comment : null}</div>
                    </p>
                  );
                } else {
                  return (
                    <p>
                      <strong>
                        {i[1].title} :
                        <strong style={{ color: 'red' }}>{i[1]?.num ? i[1]?.num : null}</strong>
                      </strong>
                      <div style={{ marginBottom: 10 }}>{i[1]?.comment ? 'Коментарии' : null}</div>
                      <div>{i[1]?.comment ? i[1]?.comment : null}</div>
                    </p>
                  );
                }
              }
            })}
          </Modal>
        </>
        <div id="filter" className="filter__wrapper">
          {keys?.map((fil: any) => {
            if (fil[2] == 'a-z') {
              return (
                <div className="wrapper__item-filter">
                  <div className="item__filter-title">{fil[0]}</div>
                  <Radio.Group
                    defaultValue={null}
                    name={fil[1]}
                    buttonStyle="solid"
                    onChange={onFilterClick}
                  >
                    <Radio.Button value="a">
                      <SortAscendingOutlined value="a" name="title" />
                    </Radio.Button>
                    <Radio.Button value="z">
                      <SortDescendingOutlined value="z" name="title" />
                    </Radio.Button>
                    <Radio.Button value={null}>Не выбрано</Radio.Button>
                  </Radio.Group>
                </div>
              );
            } else {
              if (fil[2] == 'int') {
                return (
                  <div className="wrapper__item-filter">
                    <div className="item__filter-title">{fil[0]}</div>
                    <Form onFinish={onInputChange} key={fil[1]}>
                      <Form.Item name={fil[1]}>
                        <Input type="number" name={fil[1]} min={0} onChange={onInputChange}></Input>
                      </Form.Item>
                    </Form>
                  </div>
                );
              } else {
                if (fil[1] == 'overstatement') {
                  return (
                    <div className="wrapper__item-filter">
                      <div className="item__filter-title">Завышение</div>
                      <Select
                        defaultValue="Выберите"
                        style={{ width: 120 }}
                        onChange={optionsClick}
                      >
                        <Option name={fil[1]} value={null}>
                          Выберите
                        </Option>
                        <Option name={fil[1]} value="x2">
                          x2
                        </Option>
                        <Option name={fil[1]} value="x3">
                          x3
                        </Option>
                        <Option name={fil[1]} value="x5">
                          x5
                        </Option>
                        <Option name={fil[1]} value="Запрещено">
                          Запрещено
                        </Option>
                      </Select>
                    </div>
                  );
                } else
                  return (
                    <div className="wrapper__item-filter">
                      <div className="item__filter-title">{fil[0]}</div>
                      <Radio.Group
                        defaultValue={null}
                        name={fil[1]}
                        buttonStyle="solid"
                        onChange={onFilterClick}
                      >
                        <Radio.Button value={true}>Да</Radio.Button>
                        <Radio.Button value={false}>Нет</Radio.Button>
                        <Radio.Button value={null}>Не выбрано</Radio.Button>
                      </Radio.Group>
                    </div>
                  );
              }
            }
          })}
        </div>
      </section>
    </>
  );
};

export default CardBank;
