import React, { useState, useContext } from 'react';
import 'antd/dist/antd.css';
import { Form, Input, Button, Checkbox, notification, message, Tabs } from 'antd';
import poster from '../files-img-video/poster.jpg';
import { getLogin, API } from '../api/api';
import { ContextData } from '../../context/Data/ContextData';
const Login = () => {
  const { TabPane } = Tabs;
  const { data, setData } = useContext(ContextData);
  const [regData, setRegData] = useState(false);

  if (typeof localStorage.access_token !== 'undefined' && localStorage.access_token !== null) {
    setData(true);
  }
  const onFinishFailed = (errorInfo: any) => {
    console.log('Failed:', errorInfo);
  };
  const onFinish = (values: any) => {
    const catchError = (error: any) => {
      notification.open({ message: 'Не верный пароль' });
      message.error('Не верный пароль');
    };
    getLogin(values.email, values.password)
      .then((res) => {
        console.log(res);
        if (res.error == 'Unauthorized') {
          catchError(res);
        } else if (res.access_token) {
          notification.open({ message: 'Успешно' });
          setData(true);
        }
      })
      .catch(catchError);
  };
  const swapReg = (param: any) => {
    setRegData(param);
  };
  const registerLogin = (values: any) => {
    // getRegistration(values.email, values.password, values.name)
    API.regPostFun(values.email, values.password, values.name)
      .then((res: any) => {
        if (res.ok === false && res.status === 500) {
          notification.open({ message: 'Вы уже зарегистрированы' });
          console.log(res);
        } else if (res.ok === true && res.status === 200) {
          notification.open({ message: 'Регистрация успешна' });
          swapReg(true);
        }
      })
      .catch(() => {
        notification.open({ message: 'Упс что-то пошло не так' });
      });
  };

  return (
    <>
      <div className="login__wrapper">
        <img
          src="https://static.tildacdn.com/tild3061-6639-4964-b664-353665643661/GCG.png"
          className="img__logo"
          alt=""
        />
        <video
          className="login-video"
          poster={poster}
          muted={true}
          loop
          autoPlay={true}
          src="https://plasmakeshe.ru/wp-content/themes/cx/video/wallhaven.mp4"
        ></video>
        <div className="login__from-wrapper">
          <Tabs type="card">
            <TabPane tab="Вход" key="1">
              <Form
                name="basic"
                labelCol={{
                  span: 8,
                }}
                wrapperCol={{
                  span: 16,
                }}
                initialValues={{
                  remember: true,
                }}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
                autoComplete="off"
              >
                <Form.Item
                  label="Email"
                  name="email"
                  rules={[
                    {
                      required: true,
                      message: 'пожалуйста заполните полепа',
                    },
                  ]}
                >
                  <Input />
                </Form.Item>

                <Form.Item
                  label="Пароль"
                  name="password"
                  rules={[
                    {
                      required: true,
                      message: 'пожалуйста введите пароль!',
                    },
                  ]}
                >
                  <Input.Password />
                </Form.Item>

                <Form.Item
                  name="remember"
                  valuePropName="checked"
                  wrapperCol={{
                    offset: 8,
                    span: 16,
                  }}
                >
                  <Checkbox>Запомнить пароль</Checkbox>
                </Form.Item>

                <Form.Item
                  wrapperCol={{
                    offset: 8,
                    span: 16,
                  }}
                >
                  <Button type="primary" htmlType="submit">
                    Авторизация
                  </Button>
                </Form.Item>
              </Form>
            </TabPane>
            <TabPane tab="Регистрация" key="2">
              {regData ? (
                <> Регистрация Успешна</>
              ) : (
                <Form
                  name="registerLogin"
                  labelCol={{
                    span: 8,
                  }}
                  wrapperCol={{
                    span: 16,
                  }}
                  initialValues={{
                    remember: true,
                  }}
                  onFinish={registerLogin}
                  onFinishFailed={onFinishFailed}
                  autoComplete="off"
                >
                  <Form.Item
                    label="Имя"
                    name="name"
                    rules={[
                      {
                        required: true,
                        message: 'пожалуйста заполните полепа',
                      },
                    ]}
                  >
                    <Input />
                  </Form.Item>
                  <Form.Item
                    label="Email"
                    name="email"
                    rules={[
                      {
                        required: true,
                        message: 'пожалуйста заполните полепа',
                      },
                    ]}
                  >
                    <Input />
                  </Form.Item>
                  <Form.Item
                    label="Пароль"
                    name="password"
                    rules={[
                      {
                        required: true,
                        message: 'пожалуйста введите пароль!',
                      },
                    ]}
                  >
                    <Input.Password />
                  </Form.Item>
                  <Form.Item
                    name="remember"
                    valuePropName="checked"
                    wrapperCol={{
                      offset: 8,
                      span: 16,
                    }}
                  >
                    <Checkbox>Запомнить пароль</Checkbox>
                  </Form.Item>

                  <Form.Item
                    wrapperCol={{
                      offset: 8,
                      span: 16,
                    }}
                  >
                    <Button type="primary" htmlType="submit">
                      Авторизация
                    </Button>
                  </Form.Item>
                </Form>
              )}
            </TabPane>
          </Tabs>
        </div>
      </div>
    </>
  );
};

export default Login;
