import React, { FC, useContext, useEffect, useState } from 'react';
import 'antd/dist/antd.css';
import { Button, Layout, Menu, notification } from 'antd';
import { UploadOutlined, UserOutlined, VideoCameraOutlined } from '@ant-design/icons';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import Login from '../login-authorization/Login';
import CardBank from '../bank-components/CardBank';
import News from '../news/News';
import AdminBank from '../admin-bank/Adminbank';
import { ContextData } from '../../context/Data/ContextData';
import { firstNewsApi, getLogautApi, getRefreshTokin } from '../api/api';

const RenderBlock = () => {
  const { Content, Footer, Sider } = Layout;
  const { data, setData } = useContext(ContextData);
  const [dataRoles, setDataRoles] = useState(false);
  const [dataNewNews, setDataNewNews] = useState();

  useEffect(() => {
    if (localStorage.role === '93160' || localStorage.role === '93260') {
      setDataRoles(true);
    } else {
      setDataRoles(false);
    }
  }, []);
  const logautBtn = () => {
    getLogautApi();
    localStorage.removeItem('role');
    localStorage.removeItem('access_token');
    setData(false);
  };

  useEffect(() => {
    setInterval(() => {
      const catchError = () => {
        getRefreshTokin()
          .then((res: any) => res.json())
          .then((res) => {
            console.log(res);
            localStorage.access_token = res.access_token;
            firstNewsApi();
          })
          .catch(() => {
            localStorage.removeItem('access_token');
          });
      };
      firstNewsApi()
        .then((res: any) => res.json())
        .then((res: any) => {
          if (res === false) {
            setDataNewNews(res);
            return null;
          } else if (res.message) {
            catchError();
          } else {
            setDataNewNews(res);
            notification.open({ message: res });
          }
        })
        .catch(catchError);
    }, 90000);
  }, []);

  return (
    <>
      <Layout style={{ minHeight: 1000 }}>
        <Sider
          breakpoint="lg"
          collapsedWidth="0"
          onBreakpoint={(broken) => {
            //console.log(broken);
          }}
          onCollapse={(collapsed, type) => {
            //console.log(collapsed, type);
          }}
        >
          <div className="logo" style={{ padding: 10 }}>
            <img
              src="https://static.tildacdn.com/tild3061-6639-4964-b664-353665643661/GCG.png"
              alt=""
              style={{ maxHeight: 50 }}
            />
          </div>
          <Menu theme="dark" mode="inline" defaultSelectedKeys={['1']}>
            <Menu.Item key="1" icon={<UserOutlined />}>
              <Link to="/">Банки</Link>
            </Menu.Item>
            <Menu.Item key="2" icon={<VideoCameraOutlined />}>
              <Link to="/news">Новости</Link>
            </Menu.Item>
            {dataRoles ? (
              <Menu.Item key="3" icon={<UploadOutlined />}>
                <Link to="/adminBank">Админ банк</Link>
              </Menu.Item>
            ) : null}
          </Menu>
          <Button type="primary" onClick={logautBtn} style={{ marginLeft: 24 }}>
            Выход
          </Button>
        </Sider>
        <Layout>
          {/* <Header className="site-layout-sub-header-background" style={{ padding: 0 }} /> */}
          <Content style={{ margin: '24px 16px 0' }}>
            <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
              <Routes>
                <Route path="/" element={<CardBank />} />
                <Route path="/news" element={<News />} />
                {dataRoles ? <Route path="/adminBank" element={<AdminBank />} /> : null}
              </Routes>
            </div>
          </Content>
          <Footer style={{ textAlign: 'center' }}>
            <div className="logo" style={{ padding: 10 }}>
              <img
                src="https://static.tildacdn.com/tild3061-6639-4964-b664-353665643661/GCG.png"
                alt=""
                style={{ maxHeight: 50 }}
              />
            </div>
            © Copyright {new Date().getFullYear()}. Все права защищены. Goszakaz Consulting Group
          </Footer>
        </Layout>
      </Layout>
    </>
  );
};

export default RenderBlock;
