import React, { useEffect, useState } from 'react';
import { allNews, getRefreshTokin } from '../api/api';
import './News.css';
const News = () => {
  const [newsGet, setNewsGet] = useState([]);

  useEffect(() => {
    const catchError = () => {
      getRefreshTokin()
        .then((res: any) => res.json())
        .then((res: any) => {
          console.log(res);
          localStorage.access_token = res.access_token;
          allNews();
        })
        .catch(() => {
          localStorage.removeItem('access_token');
        });
    };

    allNews()
      .then((res: any) => res.json())
      .then((res: any) => {
        //console.log(res);
        if (res.message) {
          catchError();
        } else {
          setNewsGet(res);
        }
      })
      .catch(catchError);
  }, []);

  return (
    <>
      <section className="news__section">
        <div className="container__news">
          <div className="wrapper__news">
            {newsGet
              ? newsGet?.map((i: any) => {
                  return (
                    <div key={`news_${i?.id}`} className="news__item-wprapper">
                      <div className="news__item-time">{i?.date}</div>
                      <div className="news__item-title">{i?.title}</div>
                      <div className="news__item-content">{i?.text_news}</div>
                    </div>
                  );
                })
              : null}
          </div>
        </div>
      </section>
    </>
  );
};

export default News;
