import React, { useState, useEffect } from 'react';
//import ReactDOM from 'react-dom';
import 'antd/dist/antd.css';
import './Adminbank.css';
//import { ApiBanks } from '../api/api';
import { Modal, Button, Form, Input, Select, InputNumber, Switch, Checkbox } from 'antd';
import {
  getBanks,
  refreshToken,
  updateBank,
  deleteBank,
  changeBankVisible,
  bankConstruct,
  createBank,
} from '../api/api';
import Secret from './Secret-name';
//import { findConfigFile } from 'typescript';
//import { Link, Route } from 'react-router-dom';

const AdminBank = () => {
  //Для показа модалки изменения
  const [isModalVisible, setIsModalVisible] = useState(false);
  // для показа модалки удалить?
  const [isModalDeleteVisible, setIsModalDeleteVisible] = useState(false);
  //для обновления списка банков
  const [banksUpdate, setBanksUpdate] = useState(false);
  //Один банк для вывода всех полей
  const [data, setData] = useState<any>({});
  //Все банки
  const [bankData, setBankData] = useState([]);
  //ID банка
  const [bankID, setBankID] = useState();
  //Объект хранит все checkBox значения, на которые нажали
  const arrCheck: any = {};
  //для отправки измененных значений из формы
  const [form] = Form.useForm();

  //для вызова всех банков, а также Resresh старого токена
  useEffect(() => {
    getBanks().then((res) => {
      if (res === 'Trying to refresh...') {
        refreshToken().then((refRes) => setBankData(refRes));
      } else {
        setBankData(res);
      }
    });
    //Если обновился этот параметр то обновляет список банков
  }, [banksUpdate]);

  //Для модалки изменения
  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
    form.resetFields();
  };
  //............................

  //для модалки удалить
  const showModalDelete = () => {
    setIsModalDeleteVisible(true);
  };
  const handleOkDelete = () => {
    setIsModalDeleteVisible(false);
    deleteBank(bankID).then((res) => console.log(res));
    if (banksUpdate) setBanksUpdate(false);
    else setBanksUpdate(true);
  };
  const handleCancelDelete = () => {
    setIsModalDeleteVisible(false);
  };
  //............................

  //для изменения visible (показывать/не показывать) банк
  const changeVisible = (e: any) => {
    changeBankVisible(e.target.id, e.target.checked).then((res) => console.log(res));
  };

  //вызывется для ввода изменений в банки
  const onFinish = (values: any) => {
    Object.assign(values, arrCheck); // объект с значениями checkbox
    const sendJSON: any = {}; //для создания объекта для отправки на api изменить банк

    let previousVal = 'null';
    for (const val in values) {
      if (previousVal + '_c' === val || previousVal === val + '_c') {
        if (values[previousVal] === null) {
          values[previousVal] = 0;
        }
        if (typeof values[val] !== 'undefined' && typeof values[previousVal] !== 'undefined') {
          sendJSON[previousVal] = {
            value: values[previousVal],
            comment: values[val],
          };
        } else if (typeof values[previousVal] !== 'undefined') {
          sendJSON[previousVal] = {
            value: values[previousVal],
          };
        } else if (typeof values[val] !== 'undefined') {
          sendJSON[previousVal] = {
            comment: values[val],
          };
        }
      } else if ((val === 'title' || val === 'logo') && typeof values[val] != 'undefined') {
        sendJSON[val] = values[val];
      }

      previousVal = val;
    }

    if (Object.keys(sendJSON).length) {
      if (data.id === 'createBank') {
        createBank(sendJSON).then((res) => console.log(res));
      } else {
        updateBank(sendJSON, bankID).then((res) => console.log(res));
      }

      handleOk();
      //для обновления списка банков после изменения одного из них
      if (banksUpdate) setBanksUpdate(false);
      else setBanksUpdate(true);
    }
  };
  //..................................

  //Создание объекта для измененных checkbox - ов
  const checkboxArrConstructor = (checkVal: any) => {
    arrCheck[checkVal.target.name] = checkVal.target.checked;
  };

  //пока в разработке...
  const onFinishFailed = (err: any) => {
    console.log('failed');
  };

  return (
    <section className="section__admin">
      <Button
        type="default"
        className="item__button-new-bank"
        onClick={() => {
          //вызов скелета для создания модалки для "создание банка"
          bankConstruct().then((res) => {
            setData(res);
            showModal();
          });
        }}
      >
        Добавить банк
      </Button>

      <div className="wrapper__admin">
        {
          //для отображения все банков
          bankData.map((bank) => {
            return (
              <div key={bank['id']} className="item__wrapper-admin">
                <div className="item__admin-title">{bank['title']['type']}</div>

                <Form.Item label="Показывать">
                  <Checkbox
                    defaultChecked={bank['visible']}
                    id={bank['id']}
                    name={'visible' + bank['id']}
                    onChange={changeVisible}
                  />
                </Form.Item>

                <div className="item__admin-cotent">
                  {/* ectionecti onectionectionec tionection ectione ctionectio nectionecti onectionectio nection ection ection ectionection */}
                </div>
                <div className="item__admin-modal">
                  <Button
                    type="primary"
                    onClick={() => {
                      setData(bank);
                      setBankID(bank['id']);
                      showModal();
                    }}
                  >
                    Изменить
                  </Button>
                  <Button
                    type="default"
                    className="item__button"
                    onClick={() => {
                      setBankID(bank['id']);
                      showModalDelete();
                    }}
                  >
                    Удалить
                  </Button>
                </div>
              </div>
            );
          })
        }
      </div>

      <Modal
        key={data?.id}
        title="Форма Создания/Изменения"
        visible={isModalVisible}
        afterClose={() => {
          //выполняется когда модалка закрывается
          //Для очистки data для создания полей и внесенных в них значений
          setData('');
        }}
        onOk={form.submit}
        onCancel={handleCancel}
      >
        <Form onFinish={onFinish} onFinishFailed={onFinishFailed} autoComplete="off" form={form}>
          {
            //массив для сооздания модалки изменения
            Object.entries(data).map((value: any) => {
              if (value[1]['title'] && value[0] !== 'changing_date') {
                if (Object.keys(value[1])[1] === 'filter') {
                  return (
                    <>
                      <Form.Item
                        key={value[0]}
                        label={value[1]['title'] + '(Да/Нет)?'}
                        name={value[0]}
                      >
                        <Switch
                          defaultChecked={value[1]['filter']}
                          onChange={checkboxArrConstructor}
                        />
                      </Form.Item>
                      <Form.Item
                        rules={[
                          {
                            max: 480,
                            message: 'Превышено кол-во символов(макс. 480)',
                          },
                        ]}
                        label="Комментарий"
                        key={value[0] + '_c'}
                        name={value[0] + '_c'}
                        className="item__form-labels-comment"
                      >
                        <Input.TextArea defaultValue={value[1]['comment']} />
                      </Form.Item>
                    </>
                  );
                } else if (Object.keys(value[1])[1] === 'num') {
                  return (
                    <>
                      <Form.Item
                        key={value[0]}
                        label={value[1]['title']}
                        name={value[0]}
                        className="item__form-labels"
                      >
                        <InputNumber min="0" defaultValue={value[1]['num']} />
                      </Form.Item>
                      <Form.Item
                        rules={[
                          {
                            max: 480,
                            message: 'Превышено кол-во символов(макс. 480)',
                          },
                        ]}
                        label="Комментарий"
                        key={value[0] + '_c'}
                        name={value[0] + '_c'}
                        className="item__form-labels-comment"
                      >
                        <Input.TextArea defaultValue={value[1]['comment']} />
                      </Form.Item>
                    </>
                  );
                } else if (value[0] === 'overstatement') {
                  return (
                    <>
                      <Form.Item
                        key={value[0]}
                        label={value[1]['title']}
                        name={value[0]}
                        className="item__form-labels"
                      >
                        <Select defaultValue={value[1]['type']}>
                          <Select.Option value=""> </Select.Option>
                          <Select.Option value="x2">x2</Select.Option>
                          <Select.Option value="x3">x3</Select.Option>
                          <Select.Option value="x5">x5</Select.Option>
                          <Select.Option value="Запрещено">Запрещено</Select.Option>
                        </Select>
                      </Form.Item>
                      <Form.Item
                        rules={[
                          {
                            max: 480,
                            message: 'Превышено кол-во символов(макс. 480)',
                          },
                        ]}
                        label="Комментарий"
                        key={value[0] + '_c'}
                        name={value[0] + '_c'}
                        className="item__form-labels-comment"
                      >
                        <Input.TextArea defaultValue={value[1]['comment']} />
                      </Form.Item>
                    </>
                  );
                } else if (Object.keys(value[1])[1] === 'type') {
                  if (value[0] === 'title') {
                    return (
                      <>
                        <Form.Item
                          rules={[
                            {
                              max: 50,
                              min: 3,
                              message: 'Введите от 3 до 50 символов в названии',
                            },
                          ]}
                          label={value[1]['title']}
                          key={value[0]}
                          name={value[0]}
                          className="item__form-labels-comment"
                        >
                          <Input defaultValue={value[1]['type']} />
                        </Form.Item>
                      </>
                    );
                  } else {
                    return (
                      <>
                        <Form.Item
                          rules={[
                            {
                              max: 1500,
                              message: 'Превышено кол-во символов(макс. 2000)',
                            },
                          ]}
                          label={value[1]['title']}
                          key={value[0]}
                          name={value[0]}
                          className="item__form-labels-comment"
                        >
                          <Input defaultValue={value[1]['type']} />
                        </Form.Item>
                      </>
                    );
                  }
                }
              }
            })
          }
        </Form>
      </Modal>
      <Modal
        key={data?.id + 'D'}
        title="Форма удаления"
        visible={isModalDeleteVisible}
        afterClose={() => {
          //модалка для удаления
          setData('');
        }}
        onOk={handleOkDelete}
        onCancel={handleCancelDelete}
      >
        <h3>Вы уверены?</h3>
      </Modal>
      {localStorage.role === '93260' ? <Secret /> : null}
    </section>
  );
};

export default AdminBank;
