// Вход логин
export const getLogin = (email: any, password: any) => {
  return fetch('http://127.0.0.1:8000/api/auth/login', {
    headers: {
      'Content-type': 'application/x-www-form-urlencoded; charset=UTF-8',
      Authorization: 'bearer',
    },
    method: 'POST',
    body: new URLSearchParams({
      email: email,
      password: password,
    }),
  })
    .then((res) => res.json())
    .then((res) => {
      console.log('555', res);
      if (res.error) {
      } else {
        localStorage.access_token = res.access_token;
        localStorage.role = res.role;
      }
      return res;
    });
};

