export const loginAuth = (values: any) => {
  return fetch('http://localhost:8000/api/auth/login', {
    headers: {
      'Content-type': 'application/x-www-form-urlencoded; charset=UTF-8',
    },
    method: 'POST',
    body: new URLSearchParams({
      email: values.email,
      password: values.password,
    }),
  })
    .then((res) => res.json())
    .then((commits) => {
      if (commits.access_token === undefined) {
        console.log('Sosi');
      } else {
        localStorage.access_token = commits.access_token;
        return true;
      }
    });
};

export const refreshToken = () => {
  return fetch('http://localhost:8000/api/auth/refresh', {
    headers: {
      'Content-type': 'application/x-www-form-urlencoded; charset=UTF-8',
      Accept: 'application/json',
      Authorization: 'Bearer ' + localStorage.access_token,
    },
    method: 'POST',
  })
    .then((res) => res.json())
    .then((res) => {
      if (res.access_token) {
        localStorage.access_token = res.access_token;
        console.log(res);
        return getBanks();
      } else if (res.message === 'The token has been blacklisted') {
        console.log('The token has been blacklisted');
        return 'The token has been blacklisted';
      } else {
        console.log('ErrorRefreshToken');
        return 'ErrorRefreshToken';
      }
    });
};

